html {
  background-color: white;
  background-image: url('https://wallpapercave.com/wp/wp8794617.jpg');
  background-position: center;
  background-size: cover;
}

.title {
  font-size: 28px;
  display: flex;
  color: whitesmoke;
  font-family: 'Press Start 2P', cursive;
  font-weight: 100;
  align-items: center;
  justify-content: center;
  margin-top: 4vh;
  margin-bottom: 2vh;
}
.time-score {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  margin-bottom: 1.5vh;
}
.timer {
  // background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: white;
  font-family: 'Press Start 2P', cursive;
  font-weight: 100;
}
.music {
  font-size: 14px;
  color: white;
  font-family: 'Press Start 2P', cursive;
  font-weight: 100;
  border: 3px solid white;
  padding: 5px;
  cursor: pointer;
}
.music:hover {
  background-color: white;
  color: black;
}

.restart {
  font-size: 14px;
  color: white;
  font-family: 'Press Start 2P', cursive;
  font-weight: 100;
  border: 3px solid white;
  padding: 5px;
  cursor: pointer;
}
.kill-count {
  // background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-family: 'Press Start 2P', cursive;
  font-weight: 100;
  color: white;
}
#mainContainer {
  width: 80%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
#timer-fighter {
  border-radius: 10px;
  border: 4px solid rgb(72, 72, 72);
  z-index: 1;
}

#menu {
  border-radius: 10px;
  width: 1024px;
  height: 576px;
  z-index: 3;
  position: absolute;
  background-color: rgb(65, 65, 65);
  border: 4px solid rgb(72, 72, 72);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;

  .menu-item {
    color: rgb(0, 0, 0);
    font-size: 28px;
    background-color: gold;
    font-family: 'Press Start 2P', cursive;
    width: fit-content;
    padding: 5px;
    margin-top: 5px;
  }
  .options {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;

    p {
      font-size: 16px;
      font-family: 'Press Start 2P', cursive;
      font-weight: bold;
      color: white;
      margin: 2px;
      padding: 5px;
    }
  }
  .game-options {
    padding: 10px;
    font-family: 'Press Start 2P', cursive;
    border: 4px solid rgb(72, 72, 72);
    font-size: 12px;
    //   font-weight: bold;
    color: white;
    background-color: #16191a;
    cursor: pointer;
  }

  .game-options option {
    font-size: 16px;
    font-family: 'Press Start 2P', cursive;
    font-weight: bold;
    color: #333;
    background-color: #ccc;
  }

  .game-options option:checked {
    background-color: #333;
    color: #fff;
  }
}

#displayResults {
  margin-left: auto;
  margin-right: auto;
  height: auto;
  border-radius: 10px;
  width: 1024px;
  height: 576px;
  z-index: 2;
  position: absolute;
  background-color: rgb(65, 65, 65);
  background-image: url('https://i.postimg.cc/cJSGTLQx/Krillin-wallpaper.jpg');
  background-position: center;
  background-size: cover;
  border: 4px solid rgb(72, 72, 72);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  display: none;
}

#results {
  text-shadow: 0 0 1px #a3781a, 0 0 2px #fff, 0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff, 0 0 4px #fff;;
  color: rgba(0, 0, 0, 0.758);
  font-size: 32px;
  font-family: 'helvetica';
    font-weight: 400;
    letter-spacing: -1px;
  line-height: 28px;
  position: absolute;
  left: 15%;
  bottom: 10%;
  background-color: #ae6f0982;
  padding: 10px;
  border-radius: 8px;
}

.bottom-buttons {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  gap: 25px;
  margin-top: 1.5vh;
}

.instructions {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: #16191a;
  border-radius: 8px;
  border: 4px solid rgb(72, 72, 72);
  padding: 25px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  max-width: 400px;
  .ptitle {
    color: gold;
    background-color: transparent;
    font-size: 12px;
  }
}
#instruction {
  //   margin-top: 4vh;
  color: whitesmoke;
  font-family: 'Press Start 2P', cursive;
  font-weight: 0;
  line-height: 18px;
  font-size: 10px;
}
